import { Shield, Check } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';

interface UserPermissionsProps {
  permissions: string[];
}

// Group permissions by resource
function groupPermissions(permissions: string[]): Record<string, string[]> {
  const groups: Record<string, string[]> = {};

  for (const perm of permissions) {
    const [resource, action] = perm.split(':');
    if (!groups[resource]) {
      groups[resource] = [];
    }
    groups[resource].push(action);
  }

  return groups;
}

// Resource display names
const resourceLabels: Record<string, string> = {
  incident: 'Incidents',
  workorder: 'Work Orders',
  program: 'Programs',
  school: 'Schools',
  device: 'Devices',
  serviceshop: 'Service Shops',
  inventory: 'Inventory',
  audit: 'Audit Logs',
  settings: 'Settings',
  user: 'Users',
};

// Action icons/colors
const actionStyles: Record<string, { color: string }> = {
  read: { color: 'text-blue-600' },
  create: { color: 'text-green-600' },
  update: { color: 'text-yellow-600' },
  delete: { color: 'text-red-600' },
};

export function UserPermissions({ permissions }: UserPermissionsProps) {
  const grouped = groupPermissions(permissions);
  const resources = Object.keys(grouped).sort();

  if (permissions.length === 0) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Shield className="h-5 w-5" />
            Permissions
          </CardTitle>
          <CardDescription>
            No specific permissions assigned
          </CardDescription>
        </CardHeader>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Shield className="h-5 w-5" />
          Permissions
        </CardTitle>
        <CardDescription>
          Access rights based on your role
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {resources.map((resource) => (
            <div key={resource} className="rounded-md border border-gray-100 p-3">
              <h4 className="mb-2 text-sm font-medium text-gray-900">
                {resourceLabels[resource] || resource}
              </h4>
              <div className="flex flex-wrap gap-2">
                {grouped[resource].map((action) => (
                  <span
                    key={action}
                    className={`inline-flex items-center gap-1 rounded-full bg-gray-100 px-2.5 py-0.5 text-xs font-medium ${
                      actionStyles[action]?.color || 'text-gray-600'
                    }`}
                  >
                    <Check className="h-3 w-3" />
                    {action}
                  </span>
                ))}
              </div>
            </div>
          ))}
        </div>
      </CardContent>
    </Card>
  );
}
