import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import api from './client';
import type {
  SchoolServiceProgram,
  CreateProgramRequest,
  ServicePhase,
  SiteSurvey,
  ProgramStatus,
  PhaseStatus,
  PaginatedResponse,
} from '@/types';

const PROGRAMS_KEY = 'programs';
const PHASES_KEY = 'phases';
const SURVEYS_KEY = 'surveys';

// Programs
export function usePrograms(filters: { status?: ProgramStatus; limit?: number; cursor?: string } = {}) {
  return useQuery({
    queryKey: [PROGRAMS_KEY, filters],
    queryFn: () => api.get<PaginatedResponse<SchoolServiceProgram>>('/programs', filters),
    staleTime: 30_000,
  });
}

export function useProgram(id: string) {
  return useQuery({
    queryKey: [PROGRAMS_KEY, id],
    queryFn: () => api.get<SchoolServiceProgram>(`/programs/${id}`),
    enabled: !!id,
  });
}

export function useCreateProgram() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (data: CreateProgramRequest) =>
      api.post<SchoolServiceProgram>('/programs', data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [PROGRAMS_KEY] });
    },
  });
}

// Phases
export function usePhases(programId: string) {
  return useQuery({
    queryKey: [PROGRAMS_KEY, programId, PHASES_KEY],
    queryFn: () => api.get<PaginatedResponse<ServicePhase>>(`/programs/${programId}/phases`),
    enabled: !!programId,
  });
}

export function useCreatePhase() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({
      programId,
      phaseType,
      ownerRole,
      startDate,
      endDate,
      notes,
    }: {
      programId: string;
      phaseType: string;
      ownerRole?: string;
      startDate?: string;
      endDate?: string;
      notes?: string;
    }) =>
      api.post<ServicePhase>(`/programs/${programId}/phases`, {
        phaseType,
        ownerRole,
        startDate,
        endDate,
        notes,
      }),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({
        queryKey: [PROGRAMS_KEY, variables.programId, PHASES_KEY],
      });
    },
  });
}

export function useUpdatePhaseStatus() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({
      phaseId,
      status,
    }: {
      phaseId: string;
      programId: string;
      status: PhaseStatus;
    }) =>
      api.patch<ServicePhase>(`/phases/${phaseId}/status`, { status }),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({
        queryKey: [PROGRAMS_KEY, variables.programId, PHASES_KEY],
      });
    },
  });
}

// Surveys
export function useSurveys(programId: string) {
  return useQuery({
    queryKey: [PROGRAMS_KEY, programId, SURVEYS_KEY],
    queryFn: () => api.get<PaginatedResponse<SiteSurvey>>(`/programs/${programId}/surveys`),
    enabled: !!programId,
  });
}

export function useSurvey(surveyId: string) {
  return useQuery({
    queryKey: [SURVEYS_KEY, surveyId],
    queryFn: () => api.get<SiteSurvey>(`/surveys/${surveyId}`),
    enabled: !!surveyId,
  });
}

export function useCreateSurvey() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({
      programId,
      summary,
      risks,
    }: {
      programId: string;
      summary?: string;
      risks?: string;
    }) =>
      api.post<SiteSurvey>(`/programs/${programId}/surveys`, {
        summary,
        risks,
      }),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({
        queryKey: [PROGRAMS_KEY, variables.programId, SURVEYS_KEY],
      });
    },
  });
}

export function useAddSurveyRoom() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({
      surveyId,
      name,
      roomType,
      floor,
      powerNotes,
      networkNotes,
    }: {
      surveyId: string;
      name: string;
      roomType?: string;
      floor?: string;
      powerNotes?: string;
      networkNotes?: string;
    }) =>
      api.post(`/surveys/${surveyId}/rooms`, {
        name,
        roomType,
        floor,
        powerNotes,
        networkNotes,
      }),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({
        queryKey: [SURVEYS_KEY, variables.surveyId],
      });
    },
  });
}
