import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import api from './client';
import type {
  SSOTDevice,
  SSOTDeviceModel,
  SSOTDeviceStats,
  DeviceFilters,
  DeviceModelFilters,
  DeviceHistoryEntry,
  PaginatedSSOTDevicesResponse,
  SSOTDeviceModelListResponse,
  SSOTDeviceMakesResponse,
  CreateDeviceInput,
  UpdateDeviceInput,
  CreateDeviceModelInput,
  UpdateDeviceModelInput,
  BulkUpdateDevicesInput,
  BulkDeleteDevicesInput,
  ImportResult,
  ExportOptions,
} from '@/types/device';

// Query keys
const DEVICES_KEY = 'devices';
const DEVICE_KEY = 'device';
const DEVICE_MODELS_KEY = 'device-models';
const DEVICE_STATS_KEY = 'device-stats';
const DEVICE_HISTORY_KEY = 'device-history';

// ============================================================================
// Device Queries
// ============================================================================

/**
 * Fetch paginated list of devices with filters
 */
export function useDevices(filters: DeviceFilters = {}) {
  return useQuery({
    queryKey: [DEVICES_KEY, filters],
    queryFn: () => api.get<PaginatedSSOTDevicesResponse>('/ssot/devices', filters),
    staleTime: 60_000, // 1 minute
  });
}

/**
 * Fetch a single device by ID
 */
export function useDevice(id: string) {
  return useQuery({
    queryKey: [DEVICE_KEY, id],
    queryFn: () => api.get<SSOTDevice>(`/ssot/devices/${id}`),
    enabled: !!id,
    staleTime: 60_000,
  });
}

/**
 * Fetch device history/audit log
 */
export function useDeviceHistory(deviceId: string) {
  return useQuery({
    queryKey: [DEVICE_HISTORY_KEY, deviceId],
    queryFn: () => api.get<{ items: DeviceHistoryEntry[] }>(`/ssot/devices/${deviceId}/history`),
    enabled: !!deviceId,
    staleTime: 30_000,
  });
}

/**
 * Fetch device statistics
 */
export function useDeviceStats() {
  return useQuery({
    queryKey: [DEVICE_STATS_KEY],
    queryFn: () => api.get<SSOTDeviceStats>('/ssot/devices/stats'),
    staleTime: 30_000,
    refetchInterval: 60_000, // Refresh every minute
  });
}

// ============================================================================
// Device Mutations
// ============================================================================

/**
 * Create a new device
 */
export function useCreateDevice() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (data: CreateDeviceInput) =>
      api.post<SSOTDevice>('/ssot/devices', data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [DEVICES_KEY] });
      queryClient.invalidateQueries({ queryKey: [DEVICE_STATS_KEY] });
    },
  });
}

/**
 * Update an existing device
 */
export function useUpdateDevice() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({ id, data }: { id: string; data: UpdateDeviceInput }) =>
      api.patch<SSOTDevice>(`/ssot/devices/${id}`, data),
    onSuccess: (_, { id }) => {
      queryClient.invalidateQueries({ queryKey: [DEVICES_KEY] });
      queryClient.invalidateQueries({ queryKey: [DEVICE_KEY, id] });
      queryClient.invalidateQueries({ queryKey: [DEVICE_STATS_KEY] });
    },
  });
}

/**
 * Delete a device
 */
export function useDeleteDevice() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (id: string) => api.delete(`/ssot/devices/${id}`),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [DEVICES_KEY] });
      queryClient.invalidateQueries({ queryKey: [DEVICE_STATS_KEY] });
    },
  });
}

/**
 * Bulk update multiple devices
 */
export function useBulkUpdateDevices() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (data: BulkUpdateDevicesInput) =>
      api.post<{ updated: number }>('/ssot/devices/bulk-update', data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [DEVICES_KEY] });
      queryClient.invalidateQueries({ queryKey: [DEVICE_STATS_KEY] });
    },
  });
}

/**
 * Bulk delete multiple devices
 */
export function useBulkDeleteDevices() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (data: BulkDeleteDevicesInput) =>
      api.post<{ deleted: number }>('/ssot/devices/bulk-delete', data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [DEVICES_KEY] });
      queryClient.invalidateQueries({ queryKey: [DEVICE_STATS_KEY] });
    },
  });
}

// ============================================================================
// Import/Export
// ============================================================================

/**
 * Import devices from CSV file
 */
export function useImportDevices() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async (file: File) => {
      const formData = new FormData();
      formData.append('file', file);
      // Use apiClient directly for multipart/form-data
      const response = await fetch('/api/v1/ssot/devices/import', {
        method: 'POST',
        body: formData,
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('auth_token')}`,
          'X-Tenant-ID': localStorage.getItem('tenant_id') || 'demo-tenant',
        },
      });
      if (!response.ok) {
        throw new Error('Import failed');
      }
      return response.json() as Promise<ImportResult>;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [DEVICES_KEY] });
      queryClient.invalidateQueries({ queryKey: [DEVICE_STATS_KEY] });
      queryClient.invalidateQueries({ queryKey: [DEVICE_MODELS_KEY] });
    },
  });
}

/**
 * Export devices to file
 */
export function useExportDevices() {
  return useMutation({
    mutationFn: async (options: ExportOptions) => {
      const response = await fetch('/api/v1/ssot/devices/export', {
        method: 'POST',
        body: JSON.stringify(options),
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('auth_token')}`,
          'X-Tenant-ID': localStorage.getItem('tenant_id') || 'demo-tenant',
        },
      });
      if (!response.ok) {
        throw new Error('Export failed');
      }
      const blob = await response.blob();
      const filename = options.format === 'csv' ? 'devices.csv' : 'devices.xlsx';

      // Trigger download
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = filename;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);
    },
  });
}

/**
 * Download import template
 */
export function downloadImportTemplate() {
  const headers = [
    'serial',
    'assetTag',
    'make',
    'model',
    'schoolId',
    'lifecycle',
    'enrolled',
    'notes',
    'warrantyExpiry',
    'purchaseDate',
  ];
  const csvContent = headers.join(',') + '\n';
  const blob = new Blob([csvContent], { type: 'text/csv' });
  const url = window.URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = 'device_import_template.csv';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  window.URL.revokeObjectURL(url);
}

