import { Mail, Building2, Shield, Clock, CheckCircle } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { UserAvatar } from './UserAvatar';
import type { SSOUserProfile } from '@/lib/api';

interface UserProfileCardProps {
  profile: SSOUserProfile;
  compact?: boolean;
}

// Map role names to display labels
const roleLabels: Record<string, { label: string; variant: 'default' | 'secondary' | 'outline' }> = {
  ssp_admin: { label: 'Administrator', variant: 'default' },
  ssp_operator: { label: 'Operator', variant: 'secondary' },
  ssp_viewer: { label: 'Viewer', variant: 'outline' },
};

function formatDate(dateString?: string): string {
  if (!dateString) return 'Never';
  const date = new Date(dateString);
  return date.toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
  });
}

export function UserProfileCard({ profile, compact = false }: UserProfileCardProps) {
  if (compact) {
    return (
      <div className="flex items-center gap-3 p-3">
        <UserAvatar
          src={profile.avatarUrl}
          fallback={profile.displayName}
          size="md"
        />
        <div className="min-w-0 flex-1">
          <p className="truncate text-sm font-medium text-gray-900">
            {profile.displayName}
          </p>
          <p className="truncate text-xs text-gray-500">
            {profile.email || profile.username}
          </p>
        </div>
      </div>
    );
  }

  return (
    <Card>
      <CardHeader className="pb-4">
        <div className="flex items-start gap-4">
          <UserAvatar
            src={profile.avatarUrl}
            fallback={profile.displayName}
            size="xl"
          />
          <div className="flex-1 space-y-1">
            <CardTitle className="text-xl">{profile.displayName}</CardTitle>
            <p className="text-sm text-gray-500">@{profile.username}</p>
            <div className="flex flex-wrap gap-1.5 pt-1">
              {profile.roles.map((role) => {
                const roleInfo = roleLabels[role] || { label: role, variant: 'outline' as const };
                return (
                  <Badge key={role} variant={roleInfo.variant}>
                    <Shield className="mr-1 h-3 w-3" />
                    {roleInfo.label}
                  </Badge>
                );
              })}
            </div>
          </div>
        </div>
      </CardHeader>
      <CardContent className="space-y-4">
        {/* Email */}
        {profile.email && (
          <div className="flex items-center gap-3 text-sm">
            <Mail className="h-4 w-4 text-gray-400" />
            <span className="text-gray-600">{profile.email}</span>
            {profile.emailVerified && (
              <span title="Verified">
                <CheckCircle className="h-4 w-4 text-green-500" />
              </span>
            )}
          </div>
        )}

        {/* Organization */}
        {profile.organization && (
          <div className="flex items-center gap-3 text-sm">
            <Building2 className="h-4 w-4 text-gray-400" />
            <div>
              <span className="text-gray-900">{profile.organization.displayName || profile.organization.name}</span>
              {profile.organization.type && (
                <span className="ml-2 text-gray-500">({profile.organization.type})</span>
              )}
            </div>
          </div>
        )}

        {/* Last Login */}
        {profile.lastLoginAt && (
          <div className="flex items-center gap-3 text-sm">
            <Clock className="h-4 w-4 text-gray-400" />
            <span className="text-gray-600">
              Last login: {formatDate(profile.lastLoginAt)}
            </span>
          </div>
        )}

        {/* SSO Provider info */}
        {profile.ssoProvider && (
          <div className="mt-4 rounded-md bg-gray-50 p-3">
            <p className="text-xs text-gray-500">
              Signed in via <span className="font-medium">{profile.ssoProvider}</span>
            </p>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
