import { NavLink } from 'react-router-dom';
import {
  LayoutDashboard,
  AlertTriangle,
  Wrench,
  Layers,
  Store,
  School,
  Laptop,
  Package,
  RefreshCw,
  FileText,
  Settings,
  ChevronLeft,
  ChevronRight,
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';

interface SidebarProps {
  collapsed: boolean;
  onToggle: () => void;
}

const navItems = [
  { title: 'Overview', href: '/overview', icon: LayoutDashboard },
  { title: 'Incidents', href: '/incidents', icon: AlertTriangle },
  { title: 'Work Orders', href: '/work-orders', icon: Wrench },
  { title: 'Programs', href: '/programs', icon: Layers },
  { title: 'Service Shops', href: '/service-shops', icon: Store },
  { title: 'Schools', href: '/schools', icon: School },
  { title: 'Devices', href: '/devices', icon: Laptop },
  { title: 'Parts Catalog', href: '/parts-catalog', icon: Package },
  { title: 'SSOT Sync', href: '/ssot-sync', icon: RefreshCw },
  { title: 'Audit Logs', href: '/audit-logs', icon: FileText },
  { title: 'Settings', href: '/settings', icon: Settings },
];

export function Sidebar({ collapsed, onToggle }: SidebarProps) {
  return (
    <aside
      className={cn(
        'fixed left-0 top-16 z-40 h-[calc(100vh-4rem)] border-r border-gray-200 bg-white transition-all duration-300',
        collapsed ? 'w-16' : 'w-64'
      )}
    >
      <div className="flex h-full flex-col">
        <nav className="flex-1 space-y-1 px-2 py-4">
          {navItems.map((item) => (
            <NavLink
              key={item.href}
              to={item.href}
              className={({ isActive }) =>
                cn(
                  'flex items-center gap-3 rounded-lg px-3 py-2 text-sm font-medium transition-colors',
                  isActive
                    ? 'bg-blue-50 text-blue-700'
                    : 'text-gray-700 hover:bg-gray-100 hover:text-gray-900',
                  collapsed && 'justify-center px-2'
                )
              }
              title={collapsed ? item.title : undefined}
            >
              <item.icon className="h-5 w-5 flex-shrink-0" />
              {!collapsed && <span>{item.title}</span>}
            </NavLink>
          ))}
        </nav>

        <div className="border-t border-gray-200 p-2">
          <Button
            variant="ghost"
            size="sm"
            onClick={onToggle}
            className="w-full justify-center"
          >
            {collapsed ? (
              <ChevronRight className="h-4 w-4" />
            ) : (
              <ChevronLeft className="h-4 w-4" />
            )}
          </Button>
        </div>
      </div>
    </aside>
  );
}
