import { MapPin } from 'lucide-react';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Sheet, SheetHeader, SheetBody, SheetFooter } from '@/components/ui/sheet';
import { Tabs, TabsList, TabsTrigger, TabsContent } from '@/components/ui/tabs';
import { formatDate } from '@/lib/utils';
import { StaffList } from './StaffList';
import { InventoryList } from './InventoryList';
import type { ServiceShop, ServiceStaff } from '@/types';

interface InventoryItem {
  id: string;
  partName: string;
  partPuk: string;
  qtyOnHand: number;
  qtyAvailable: number;
  reorderLevel: number;
}

interface ServiceShopDetailProps {
  shop: ServiceShop | null;
  open: boolean;
  onClose: () => void;
  detailTab: string;
  onDetailTabChange: (tab: string) => void;
  staff: ServiceStaff[];
  inventory: InventoryItem[];
}

export function ServiceShopDetail({
  shop,
  open,
  onClose,
  detailTab,
  onDetailTabChange,
  staff,
  inventory,
}: ServiceShopDetailProps) {
  if (!shop) return null;

  return (
    <Sheet open={open} onClose={onClose} side="right">
      <SheetHeader onClose={onClose}>Service Shop Details</SheetHeader>
      <SheetBody className="p-0">
        <div className="h-full flex flex-col">
          {/* Header Info */}
          <div className="p-6 border-b border-gray-200">
            <div className="flex items-center gap-2 mb-2">
              <Badge
                className={
                  shop.active
                    ? 'bg-green-100 text-green-800'
                    : 'bg-gray-100 text-gray-800'
                }
              >
                {shop.active ? 'Active' : 'Inactive'}
              </Badge>
              <Badge variant="outline" className="capitalize">
                {shop.coverageLevel}
              </Badge>
            </div>
            <h2 className="text-lg font-semibold text-gray-900">{shop.name}</h2>
            <p className="text-sm text-gray-500 flex items-center gap-1">
              <MapPin className="h-4 w-4" />
              {shop.location || 'No location specified'}
            </p>
          </div>

          {/* Tabs */}
          <Tabs
            value={detailTab}
            onValueChange={onDetailTabChange}
            className="flex-1 flex flex-col"
          >
            <div className="border-b border-gray-200 px-6">
              <TabsList className="bg-transparent -mb-px">
                <TabsTrigger value="staff">Staff ({staff.length})</TabsTrigger>
                <TabsTrigger value="inventory">
                  Inventory ({inventory.length})
                </TabsTrigger>
                <TabsTrigger value="details">Details</TabsTrigger>
              </TabsList>
            </div>

            <div className="flex-1 overflow-auto">
              <TabsContent value="staff" className="p-6 m-0">
                <StaffList staff={staff} />
              </TabsContent>

              <TabsContent value="inventory" className="p-6 m-0">
                <InventoryList inventory={inventory} />
              </TabsContent>

              <TabsContent value="details" className="p-6 m-0">
                <div className="space-y-6">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <h3 className="text-sm font-medium text-gray-500 mb-1">
                        County
                      </h3>
                      <p className="text-gray-900">{shop.countyName}</p>
                      <p className="text-sm text-gray-500">{shop.countyCode}</p>
                    </div>
                    <div>
                      <h3 className="text-sm font-medium text-gray-500 mb-1">
                        Sub-County
                      </h3>
                      <p className="text-gray-900">{shop.subCountyName || '-'}</p>
                      <p className="text-sm text-gray-500">
                        {shop.subCountyCode || '-'}
                      </p>
                    </div>
                  </div>

                  <div>
                    <h3 className="text-sm font-medium text-gray-500 mb-1">
                      Location
                    </h3>
                    <p className="text-gray-900">
                      {shop.location || 'Not specified'}
                    </p>
                  </div>

                  <div>
                    <h3 className="text-sm font-medium text-gray-500 mb-2">
                      Timeline
                    </h3>
                    <div className="space-y-2 text-sm">
                      <div className="flex justify-between">
                        <span className="text-gray-500">Created</span>
                        <span className="text-gray-900">
                          {formatDate(shop.createdAt)}
                        </span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-gray-500">Last Updated</span>
                        <span className="text-gray-900">
                          {formatDate(shop.updatedAt)}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </TabsContent>
            </div>
          </Tabs>
        </div>
      </SheetBody>
      <SheetFooter>
        <Button variant="outline" onClick={onClose}>
          Close
        </Button>
        <Button>Edit Shop</Button>
      </SheetFooter>
    </Sheet>
  );
}
