import * as React from 'react';
import { type ColumnDef } from '@tanstack/react-table';
import {
  FileText,
  Search,
  Filter,
  Download,
  Eye,
  Plus,
  Pencil,
  Trash2,
  User,
} from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Select } from '@/components/ui/select';
import { DateRangePicker } from '@/components/ui/date-picker';
import { DataTable, SortableHeader } from '@/components/ui/data-table';
import { Sheet, SheetHeader, SheetBody, SheetFooter } from '@/components/ui/sheet';
import { useAuditLogs, useAuditLogEntityTypes, exportAuditLogs } from '@/api/audit-logs';
import { formatDate, formatRelativeTime, cn } from '@/lib/utils';
import type { AuditLog, AuditAction, AuditLogFilters } from '@/types';

const actionOptions = [
  { value: '', label: 'All Actions' },
  { value: 'create', label: 'Create' },
  { value: 'update', label: 'Update' },
  { value: 'delete', label: 'Delete' },
];

const actionColors: Record<AuditAction, string> = {
  create: 'bg-green-100 text-green-800',
  update: 'bg-blue-100 text-blue-800',
  delete: 'bg-red-100 text-red-800',
};

const actionIcons: Record<AuditAction, React.ComponentType<{ className?: string }>> = {
  create: Plus,
  update: Pencil,
  delete: Trash2,
};

export function AuditLogs() {
  // Filters state
  const [filters, setFilters] = React.useState<AuditLogFilters>({
    limit: 50,
  });
  const [searchQuery, setSearchQuery] = React.useState('');
  const [showFilters, setShowFilters] = React.useState(false);
  const [startDate, setStartDate] = React.useState<Date | null>(null);
  const [endDate, setEndDate] = React.useState<Date | null>(null);
  const [isExporting, setIsExporting] = React.useState(false);

  // Selected log for detail view
  const [selectedLog, setSelectedLog] = React.useState<AuditLog | null>(null);
  const [showDetail, setShowDetail] = React.useState(false);

  // API hooks
  const { data, isLoading } = useAuditLogs({
    ...filters,
    startDate: startDate?.toISOString(),
    endDate: endDate?.toISOString(),
  });
  const { data: entityTypes } = useAuditLogEntityTypes();

  // Table columns
  const columns: ColumnDef<AuditLog>[] = [
    {
      accessorKey: 'createdAt',
      header: ({ column }) => <SortableHeader column={column}>Timestamp</SortableHeader>,
      cell: ({ row }) => (
        <div className="text-sm">
          <div className="font-medium text-gray-900">
            {formatRelativeTime(row.original.createdAt)}
          </div>
          <div className="text-gray-500">
            {formatDate(row.original.createdAt)}
          </div>
        </div>
      ),
    },
    {
      accessorKey: 'action',
      header: 'Action',
      cell: ({ row }) => {
        const ActionIcon = actionIcons[row.original.action];
        return (
          <div className="flex items-center gap-2">
            <div className={cn(
              'flex h-8 w-8 items-center justify-center rounded-full',
              row.original.action === 'create' && 'bg-green-50',
              row.original.action === 'update' && 'bg-blue-50',
              row.original.action === 'delete' && 'bg-red-50'
            )}>
              <ActionIcon className={cn(
                'h-4 w-4',
                row.original.action === 'create' && 'text-green-600',
                row.original.action === 'update' && 'text-blue-600',
                row.original.action === 'delete' && 'text-red-600'
              )} />
            </div>
            <Badge className={cn('capitalize', actionColors[row.original.action])}>
              {row.original.action}
            </Badge>
          </div>
        );
      },
    },
    {
      accessorKey: 'entityType',
      header: 'Entity',
      cell: ({ row }) => (
        <div className="text-sm">
          <div className="font-medium text-gray-900 capitalize">
            {row.original.entityType.replace(/_/g, ' ')}
          </div>
          <div className="text-gray-500 font-mono text-xs truncate max-w-[150px]">
            {row.original.entityId}
          </div>
        </div>
      ),
    },
    {
      accessorKey: 'userEmail',
      header: 'User',
      cell: ({ row }) => (
        <div className="flex items-center gap-2">
          <div className="flex h-8 w-8 items-center justify-center rounded-full bg-gray-100">
            <User className="h-4 w-4 text-gray-600" />
          </div>
          <span className="text-sm text-gray-900 truncate max-w-[150px]">
            {row.original.userEmail}
          </span>
        </div>
      ),
    },
    {
      accessorKey: 'ipAddress',
      header: 'IP Address',
      cell: ({ row }) => (
        <span className="text-sm text-gray-500 font-mono">
          {row.original.ipAddress || '-'}
        </span>
      ),
    },
    {
      id: 'actions',
      cell: ({ row }) => (
        <Button
          variant="ghost"
          size="sm"
          onClick={(e) => {
            e.stopPropagation();
            setSelectedLog(row.original);
            setShowDetail(true);
          }}
        >
          <Eye className="h-4 w-4" />
        </Button>
      ),
    },
  ];

  // Handle filter changes
  const handleFilterChange = (key: keyof AuditLogFilters, value: string) => {
    setFilters((prev) => ({
      ...prev,
      [key]: value || undefined,
    }));
  };

  // Handle export
  const handleExport = async () => {
    setIsExporting(true);
    try {
      const blob = await exportAuditLogs({
        ...filters,
        startDate: startDate?.toISOString(),
        endDate: endDate?.toISOString(),
      });

      // Download the file
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `audit-logs-${new Date().toISOString().split('T')[0]}.csv`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
    } catch (err) {
      console.error('Failed to export audit logs:', err);
    } finally {
      setIsExporting(false);
    }
  };

  const logs = data?.items || [];
  const entityTypeOptions = [
    { value: '', label: 'All Entity Types' },
    ...(entityTypes || []),
  ];

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Audit Logs</h1>
          <p className="text-sm text-gray-500">
            View system activity and change history
          </p>
        </div>
        <Button onClick={handleExport} disabled={isExporting} variant="outline">
          <Download className="h-4 w-4" />
          {isExporting ? 'Exporting...' : 'Export CSV'}
        </Button>
      </div>

      {/* Stats Cards */}
      <div className="grid gap-4 md:grid-cols-4">
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-full bg-gray-100">
                <FileText className="h-5 w-5 text-gray-600" />
              </div>
              <div>
                <div className="text-2xl font-bold">{logs.length}</div>
                <div className="text-sm text-gray-500">Total Events</div>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-full bg-green-50">
                <Plus className="h-5 w-5 text-green-600" />
              </div>
              <div>
                <div className="text-2xl font-bold">
                  {logs.filter((l) => l.action === 'create').length}
                </div>
                <div className="text-sm text-gray-500">Creates</div>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-full bg-blue-50">
                <Pencil className="h-5 w-5 text-blue-600" />
              </div>
              <div>
                <div className="text-2xl font-bold">
                  {logs.filter((l) => l.action === 'update').length}
                </div>
                <div className="text-sm text-gray-500">Updates</div>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-full bg-red-50">
                <Trash2 className="h-5 w-5 text-red-600" />
              </div>
              <div>
                <div className="text-2xl font-bold">
                  {logs.filter((l) => l.action === 'delete').length}
                </div>
                <div className="text-sm text-gray-500">Deletes</div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="p-4">
          <div className="flex flex-wrap items-center gap-4">
            <div className="relative flex-1 min-w-[200px] max-w-md">
              <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
              <Input
                placeholder="Search by user or entity ID..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
            <Select
              value={filters.action || ''}
              onChange={(value) => handleFilterChange('action', value)}
              options={actionOptions}
              placeholder="Action"
              className="w-36"
            />
            <Select
              value={filters.entityType || ''}
              onChange={(value) => handleFilterChange('entityType', value)}
              options={entityTypeOptions}
              placeholder="Entity Type"
              className="w-44"
            />
            <Button
              variant="outline"
              onClick={() => setShowFilters(!showFilters)}
            >
              <Filter className="h-4 w-4" />
              More Filters
            </Button>
          </div>

          {showFilters && (
            <div className="mt-4 pt-4 border-t border-gray-200">
              <div className="flex flex-wrap items-center gap-4">
                <div className="flex-1 min-w-[300px]">
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Date Range
                  </label>
                  <DateRangePicker
                    startDate={startDate}
                    endDate={endDate}
                    onChange={(start, end) => {
                      setStartDate(start);
                      setEndDate(end);
                    }}
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    User ID
                  </label>
                  <Input
                    value={filters.userId || ''}
                    onChange={(e) => handleFilterChange('userId', e.target.value)}
                    placeholder="Filter by user ID"
                    className="w-48"
                  />
                </div>
                <div className="flex items-end gap-2">
                  <Button
                    variant="outline"
                    onClick={() => {
                      setFilters({ limit: 50 });
                      setSearchQuery('');
                      setStartDate(null);
                      setEndDate(null);
                    }}
                  >
                    Clear Filters
                  </Button>
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Audit Logs Table */}
      <Card>
        <CardContent className="p-0">
          <DataTable
            columns={columns}
            data={logs}
            isLoading={isLoading}
            showColumnVisibility
            onRowClick={(row) => {
              setSelectedLog(row);
              setShowDetail(true);
            }}
            emptyMessage="No audit logs found"
          />
        </CardContent>
      </Card>

      {/* Audit Log Detail Sheet */}
      <Sheet open={showDetail} onClose={() => setShowDetail(false)} side="right">
        <SheetHeader onClose={() => setShowDetail(false)}>
          Audit Log Details
        </SheetHeader>
        <SheetBody>
          {selectedLog && (
            <div className="space-y-6">
              {/* Header */}
              <div>
                <div className="flex items-center gap-2 mb-2">
                  <Badge className={cn('capitalize', actionColors[selectedLog.action])}>
                    {selectedLog.action}
                  </Badge>
                  <span className="text-sm text-gray-500 capitalize">
                    {selectedLog.entityType.replace(/_/g, ' ')}
                  </span>
                </div>
                <h2 className="text-lg font-semibold text-gray-900">
                  {selectedLog.action.charAt(0).toUpperCase() + selectedLog.action.slice(1)} Operation
                </h2>
                <p className="text-sm text-gray-500 mt-1">
                  {formatDate(selectedLog.createdAt)}
                </p>
              </div>

              {/* User Info */}
              <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <div className="flex h-10 w-10 items-center justify-center rounded-full bg-gray-200">
                  <User className="h-5 w-5 text-gray-600" />
                </div>
                <div>
                  <div className="font-medium text-gray-900">{selectedLog.userEmail}</div>
                  <div className="text-sm text-gray-500">User ID: {selectedLog.userId}</div>
                </div>
              </div>

              {/* Entity Info */}
              <div>
                <h3 className="text-sm font-medium text-gray-500 mb-2">Entity Information</h3>
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-500">Type</span>
                    <span className="text-gray-900 capitalize">{selectedLog.entityType.replace(/_/g, ' ')}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-500">ID</span>
                    <span className="text-gray-900 font-mono text-xs">{selectedLog.entityId}</span>
                  </div>
                </div>
              </div>

              {/* Request Info */}
              <div>
                <h3 className="text-sm font-medium text-gray-500 mb-2">Request Information</h3>
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-500">IP Address</span>
                    <span className="text-gray-900 font-mono">{selectedLog.ipAddress || '-'}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-500">Request ID</span>
                    <span className="text-gray-900 font-mono text-xs">{selectedLog.requestId || '-'}</span>
                  </div>
                  {selectedLog.userAgent && (
                    <div>
                      <span className="text-gray-500 block mb-1">User Agent</span>
                      <span className="text-gray-900 text-xs break-all">{selectedLog.userAgent}</span>
                    </div>
                  )}
                </div>
              </div>

              {/* State Changes */}
              {(selectedLog.beforeState || selectedLog.afterState) && (
                <div>
                  <h3 className="text-sm font-medium text-gray-500 mb-2">State Changes</h3>
                  <div className="space-y-3">
                    {selectedLog.beforeState && (
                      <div>
                        <span className="text-xs font-medium text-gray-500 block mb-1">Before</span>
                        <pre className="text-xs bg-red-50 text-red-800 p-3 rounded-lg overflow-auto max-h-40">
                          {JSON.stringify(selectedLog.beforeState, null, 2)}
                        </pre>
                      </div>
                    )}
                    {selectedLog.afterState && (
                      <div>
                        <span className="text-xs font-medium text-gray-500 block mb-1">After</span>
                        <pre className="text-xs bg-green-50 text-green-800 p-3 rounded-lg overflow-auto max-h-40">
                          {JSON.stringify(selectedLog.afterState, null, 2)}
                        </pre>
                      </div>
                    )}
                  </div>
                </div>
              )}
            </div>
          )}
        </SheetBody>
        <SheetFooter>
          <Button variant="outline" onClick={() => setShowDetail(false)}>
            Close
          </Button>
        </SheetFooter>
      </Sheet>
    </div>
  );
}
