import * as React from 'react';
import { type ColumnDef } from '@tanstack/react-table';
import { Plus, Store, Search, ChevronRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Select } from '@/components/ui/select';
import { DataTable, SortableHeader, createSelectColumn } from '@/components/ui/data-table';
import {
  ServiceShopsStats,
  ServiceShopDetail,
  CreateServiceShopModal,
} from '@/components/service-shops';
import { useServiceShops, useCreateServiceShop, useServiceStaff, useInventory } from '@/api/service-shops';
import { formatDate } from '@/lib/utils';
import type { ServiceShop, CreateServiceShopRequest } from '@/types';

const statusOptions = [
  { value: '', label: 'All Status' },
  { value: 'true', label: 'Active' },
  { value: 'false', label: 'Inactive' },
];

export function ServiceShops() {
  // Filters state
  const [filters, setFilters] = React.useState<{ active?: boolean; countyCode?: string; limit?: number }>({
    limit: 50,
  });
  const [searchQuery, setSearchQuery] = React.useState('');

  // Selected shop for detail view
  const [selectedShop, setSelectedShop] = React.useState<ServiceShop | null>(null);
  const [showDetail, setShowDetail] = React.useState(false);
  const [detailTab, setDetailTab] = React.useState('staff');

  // Create shop modal
  const [showCreateModal, setShowCreateModal] = React.useState(false);
  const [createForm, setCreateForm] = React.useState<CreateServiceShopRequest>({
    name: '',
    countyCode: '',
    countyName: '',
    subCountyCode: '',
    subCountyName: '',
    coverageLevel: 'county',
    location: '',
    active: true,
  });

  // API hooks
  const { data, isLoading } = useServiceShops(filters);
  const createShop = useCreateServiceShop();

  // Detail view data
  const { data: staffData } = useServiceStaff({ serviceShopId: selectedShop?.id });
  const { data: inventoryData } = useInventory({ serviceShopId: selectedShop?.id });

  // Table columns
  const columns: ColumnDef<ServiceShop>[] = React.useMemo(
    () => [
      createSelectColumn<ServiceShop>(),
      {
        accessorKey: 'name',
        header: ({ column }) => <SortableHeader column={column}>Shop Name</SortableHeader>,
        cell: ({ row }) => (
          <div className="flex items-center gap-3">
            <div className="flex h-8 w-8 items-center justify-center rounded-full bg-orange-50">
              <Store className="h-4 w-4 text-orange-600" />
            </div>
            <div className="min-w-0">
              <div className="font-medium text-gray-900 truncate max-w-[200px]">
                {row.original.name}
              </div>
              <div className="text-sm text-gray-500">{row.original.location}</div>
            </div>
          </div>
        ),
      },
      {
        accessorKey: 'active',
        header: 'Status',
        cell: ({ row }) => (
          <Badge className={row.original.active ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-800'}>
            {row.original.active ? 'Active' : 'Inactive'}
          </Badge>
        ),
      },
      {
        accessorKey: 'countyName',
        header: 'County',
        cell: ({ row }) => (
          <div className="text-sm">
            <div className="font-medium">{row.original.countyName}</div>
            <div className="text-gray-500">{row.original.subCountyName}</div>
          </div>
        ),
      },
      {
        accessorKey: 'coverageLevel',
        header: 'Coverage',
        cell: ({ row }) => (
          <Badge variant="outline" className="capitalize">
            {row.original.coverageLevel}
          </Badge>
        ),
      },
      {
        accessorKey: 'createdAt',
        header: ({ column }) => <SortableHeader column={column}>Created</SortableHeader>,
        cell: ({ row }) => (
          <div className="text-sm text-gray-500">
            {formatDate(row.original.createdAt)}
          </div>
        ),
      },
      {
        id: 'actions',
        cell: ({ row }) => (
          <Button
            variant="ghost"
            size="sm"
            onClick={(e) => {
              e.stopPropagation();
              setSelectedShop(row.original);
              setShowDetail(true);
              setDetailTab('staff');
            }}
          >
            <ChevronRight className="h-4 w-4" />
          </Button>
        ),
      },
    ],
    []
  );

  // Handle create shop
  const handleCreateShop = async () => {
    try {
      await createShop.mutateAsync(createForm);
      setShowCreateModal(false);
      setCreateForm({
        name: '',
        countyCode: '',
        countyName: '',
        subCountyCode: '',
        subCountyName: '',
        coverageLevel: 'county',
        location: '',
        active: true,
      });
    } catch (err) {
      console.error('Failed to create service shop:', err);
    }
  };

  const shops = data?.items || [];
  const staff = staffData?.items || [];
  const inventory = inventoryData?.items || [];

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Service Shops</h1>
          <p className="text-sm text-gray-500">Manage service shops and staff</p>
        </div>
        <Button onClick={() => setShowCreateModal(true)}>
          <Plus className="h-4 w-4" />
          Create Service Shop
        </Button>
      </div>

      {/* Stats Cards */}
      <ServiceShopsStats shops={shops} />

      {/* Filters */}
      <Card>
        <CardContent className="p-4">
          <div className="flex flex-wrap items-center gap-4">
            <div className="relative flex-1 min-w-[200px] max-w-md">
              <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
              <Input
                placeholder="Search shops..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
            <Select
              value={filters.active === undefined ? '' : String(filters.active)}
              onChange={(value) =>
                setFilters((prev) => ({
                  ...prev,
                  active: value === '' ? undefined : value === 'true',
                }))
              }
              options={statusOptions}
              placeholder="Status"
              className="w-40"
            />
          </div>
        </CardContent>
      </Card>

      {/* Service Shops Table */}
      <Card>
        <CardContent className="p-0">
          <DataTable
            columns={columns}
            data={shops}
            isLoading={isLoading}
            searchKey="name"
            searchPlaceholder="Search by name..."
            showRowSelection
            showColumnVisibility
            onRowClick={(row) => {
              setSelectedShop(row);
              setShowDetail(true);
              setDetailTab('staff');
            }}
            emptyMessage="No service shops found"
          />
        </CardContent>
      </Card>

      {/* Service Shop Detail Sheet */}
      <ServiceShopDetail
        shop={selectedShop}
        open={showDetail}
        onClose={() => setShowDetail(false)}
        detailTab={detailTab}
        onDetailTabChange={setDetailTab}
        staff={staff}
        inventory={inventory}
      />

      {/* Create Service Shop Modal */}
      <CreateServiceShopModal
        open={showCreateModal}
        onClose={() => setShowCreateModal(false)}
        formData={createForm}
        onFormChange={setCreateForm}
        onSubmit={handleCreateShop}
        isLoading={createShop.isPending}
      />
    </div>
  );
}
