import * as React from 'react';
import { type ColumnDef } from '@tanstack/react-table';
import { Plus, Layers, Search, ChevronRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Select } from '@/components/ui/select';
import { DataTable, SortableHeader, createSelectColumn } from '@/components/ui/data-table';
import {
  ProgramsStats,
  ProgramDetail,
  CreateProgramModal,
  phaseTypeLabels,
} from '@/components/programs';
import { usePrograms, useCreateProgram, usePhases, useSurveys } from '@/api/programs';
import { formatDate, cn } from '@/lib/utils';
import type { SchoolServiceProgram, ProgramStatus } from '@/types';

const statusOptions = [
  { value: '', label: 'All Statuses' },
  { value: 'active', label: 'Active' },
  { value: 'paused', label: 'Paused' },
  { value: 'completed', label: 'Completed' },
];

const statusColors: Record<ProgramStatus, string> = {
  active: 'bg-green-100 text-green-800',
  paused: 'bg-yellow-100 text-yellow-800',
  completed: 'bg-gray-100 text-gray-800',
};

export function Programs() {
  // Filters state
  const [filters, setFilters] = React.useState<{ status?: ProgramStatus; limit?: number }>({
    limit: 50,
  });
  const [searchQuery, setSearchQuery] = React.useState('');

  // Selected program for detail view
  const [selectedProgram, setSelectedProgram] = React.useState<SchoolServiceProgram | null>(null);
  const [showDetail, setShowDetail] = React.useState(false);
  const [detailTab, setDetailTab] = React.useState('phases');

  // Create program modal
  const [showCreateModal, setShowCreateModal] = React.useState(false);
  const [createForm, setCreateForm] = React.useState({
    schoolId: '',
    startDate: null as Date | null,
    goLiveDate: null as Date | null,
    notes: '',
  });

  // API hooks
  const { data, isLoading } = usePrograms(filters);
  const createProgram = useCreateProgram();

  // Detail view data
  const { data: phasesData } = usePhases(selectedProgram?.id || '');
  const { data: surveysData } = useSurveys(selectedProgram?.id || '');

  // Table columns
  const columns: ColumnDef<SchoolServiceProgram>[] = React.useMemo(
    () => [
      createSelectColumn<SchoolServiceProgram>(),
      {
        accessorKey: 'schoolId',
        header: ({ column }) => <SortableHeader column={column}>School</SortableHeader>,
        cell: ({ row }) => (
          <div className="flex items-center gap-3">
            <div className="flex h-8 w-8 items-center justify-center rounded-full bg-purple-50">
              <Layers className="h-4 w-4 text-purple-600" />
            </div>
            <div className="min-w-0">
              <div className="font-medium text-gray-900 truncate max-w-[200px]">
                {row.original.schoolId}
              </div>
              <div className="text-sm text-gray-500">
                Current: {phaseTypeLabels[row.original.currentPhase]}
              </div>
            </div>
          </div>
        ),
      },
      {
        accessorKey: 'status',
        header: 'Status',
        cell: ({ row }) => (
          <Badge className={cn('capitalize', statusColors[row.original.status])}>
            {row.original.status}
          </Badge>
        ),
      },
      {
        accessorKey: 'currentPhase',
        header: 'Current Phase',
        cell: ({ row }) => (
          <div className="text-sm">
            <div className="font-medium">{phaseTypeLabels[row.original.currentPhase]}</div>
          </div>
        ),
      },
      {
        accessorKey: 'startDate',
        header: 'Start Date',
        cell: ({ row }) => (
          <div className="text-sm text-gray-500">
            {row.original.startDate ? formatDate(row.original.startDate) : '-'}
          </div>
        ),
      },
      {
        accessorKey: 'goLiveDate',
        header: 'Go-Live Date',
        cell: ({ row }) => (
          <div className="text-sm text-gray-500">
            {row.original.goLiveDate ? formatDate(row.original.goLiveDate) : '-'}
          </div>
        ),
      },
      {
        id: 'actions',
        cell: ({ row }) => (
          <Button
            variant="ghost"
            size="sm"
            onClick={(e) => {
              e.stopPropagation();
              setSelectedProgram(row.original);
              setShowDetail(true);
              setDetailTab('phases');
            }}
          >
            <ChevronRight className="h-4 w-4" />
          </Button>
        ),
      },
    ],
    []
  );

  // Handle create program
  const handleCreateProgram = async () => {
    try {
      await createProgram.mutateAsync({
        schoolId: createForm.schoolId,
        startDate: createForm.startDate?.toISOString(),
        goLiveDate: createForm.goLiveDate?.toISOString(),
        notes: createForm.notes || undefined,
      });
      setShowCreateModal(false);
      setCreateForm({
        schoolId: '',
        startDate: null,
        goLiveDate: null,
        notes: '',
      });
    } catch (err) {
      console.error('Failed to create program:', err);
    }
  };

  const programs = data?.items || [];
  const phases = phasesData?.items || [];
  const surveys = surveysData?.items || [];

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Programs</h1>
          <p className="text-sm text-gray-500">
            Manage school service programs and phases
          </p>
        </div>
        <Button onClick={() => setShowCreateModal(true)}>
          <Plus className="h-4 w-4" />
          Create Program
        </Button>
      </div>

      {/* Stats Cards */}
      <ProgramsStats programs={programs} />

      {/* Filters */}
      <Card>
        <CardContent className="p-4">
          <div className="flex flex-wrap items-center gap-4">
            <div className="relative flex-1 min-w-[200px] max-w-md">
              <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
              <Input
                placeholder="Search programs..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
            <Select
              value={filters.status || ''}
              onChange={(value) => setFilters((prev) => ({ ...prev, status: value as ProgramStatus || undefined }))}
              options={statusOptions}
              placeholder="Status"
              className="w-40"
            />
          </div>
        </CardContent>
      </Card>

      {/* Programs Table */}
      <Card>
        <CardContent className="p-0">
          <DataTable
            columns={columns}
            data={programs}
            isLoading={isLoading}
            searchKey="schoolId"
            searchPlaceholder="Search by school..."
            showRowSelection
            showColumnVisibility
            onRowClick={(row) => {
              setSelectedProgram(row);
              setShowDetail(true);
              setDetailTab('phases');
            }}
            emptyMessage="No programs found"
          />
        </CardContent>
      </Card>

      {/* Program Detail Sheet */}
      <ProgramDetail
        program={selectedProgram}
        open={showDetail}
        onClose={() => setShowDetail(false)}
        detailTab={detailTab}
        onDetailTabChange={setDetailTab}
        phases={phases}
        surveys={surveys}
      />

      {/* Create Program Modal */}
      <CreateProgramModal
        open={showCreateModal}
        onClose={() => setShowCreateModal(false)}
        formData={createForm}
        onFormChange={setCreateForm}
        onSubmit={handleCreateProgram}
        isLoading={createProgram.isPending}
      />
    </div>
  );
}
