import * as React from 'react';
import {
  Search,
  RefreshCw,
  Laptop,
  Package,
  Wrench,
  Archive,
  Filter,
  X,
} from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Select } from '@/components/ui/select';
import {
  useDevices,
  useDeviceStats,
  useDeviceModels,
  useDeviceMakes,
  useSyncDevicesFromSSO,
} from '@/api/devices';
import { useSchools } from '@/api/ssot';
import { toast } from '@/lib/toast';
import { cn } from '@/lib/utils';
// SSOTDevice and DeviceLifecycleStatus types imported for reference
// import type { SSOTDevice, DeviceLifecycleStatus } from '@/types/device';

// Status colors for badges
const STATUS_COLORS: Record<string, string> = {
  in_stock: 'bg-green-100 text-green-800',
  deployed: 'bg-blue-100 text-blue-800',
  repair: 'bg-yellow-100 text-yellow-800',
  retired: 'bg-gray-100 text-gray-800',
};

// Status icons
const STATUS_ICONS: Record<string, React.ReactNode> = {
  in_stock: <Package className="h-4 w-4" />,
  deployed: <Laptop className="h-4 w-4" />,
  repair: <Wrench className="h-4 w-4" />,
  retired: <Archive className="h-4 w-4" />,
};

function formatStatus(status: string): string {
  return status.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase());
}

export function DevicesPage() {
  // Filters
  const [searchQuery, setSearchQuery] = React.useState('');
  const [statusFilter, setStatusFilter] = React.useState<string>('');
  const [schoolFilter, setSchoolFilter] = React.useState<string>('');
  const [page, setPage] = React.useState(0);
  const limit = 50;

  // Build filters object
  const filters = React.useMemo(() => ({
    q: searchQuery || undefined,
    status: statusFilter || undefined,
    schoolId: schoolFilter || undefined,
    limit,
    offset: page * limit,
  }), [searchQuery, statusFilter, schoolFilter, page]);

  // API queries
  const { data: devicesData, isLoading: devicesLoading, refetch } = useDevices(filters);
  const { data: statsData, isLoading: statsLoading } = useDeviceStats();
  const { data: modelsData } = useDeviceModels();
  const { data: makesData } = useDeviceMakes();
  const { data: schoolsData } = useSchools({ limit: 1000 });

  // Mutations
  const syncDevices = useSyncDevicesFromSSO();

  // Derived data
  const devices = devicesData?.items || [];
  const totalDevices = devicesData?.total || 0;
  const models = modelsData?.items || [];
  const makes = makesData?.makes || [];
  const schools = schoolsData?.items || [];

  // School options for filter
  const schoolOptions = React.useMemo(() => {
    return [
      { value: '', label: 'All Schools' },
      ...schools.map((s) => ({
        value: s.schoolId,
        label: s.name,
      })),
    ];
  }, [schools]);

  // Status options for filter
  const statusOptions = [
    { value: '', label: 'All Statuses' },
    { value: 'in_stock', label: 'In Stock' },
    { value: 'deployed', label: 'Deployed' },
    { value: 'repair', label: 'In Repair' },
    { value: 'retired', label: 'Retired' },
  ];

  // Handle sync
  const handleSync = async () => {
    try {
      const result = await syncDevices.mutateAsync();
      toast.success(`Synced ${result.synced} devices from SSOT`);
      refetch();
    } catch {
      toast.error('Failed to sync devices');
    }
  };

  // Clear all filters
  const clearFilters = () => {
    setSearchQuery('');
    setStatusFilter('');
    setSchoolFilter('');
    setPage(0);
  };

  const hasFilters = searchQuery || statusFilter || schoolFilter;

  // Pagination
  const totalPages = Math.ceil(totalDevices / limit);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-semibold text-gray-900">SSOT Devices</h1>
          <p className="text-sm text-gray-500 mt-1">
            Device inventory from Single Source of Truth
          </p>
        </div>
        <div className="flex items-center gap-3">
          <Button
            variant="outline"
            onClick={handleSync}
            disabled={syncDevices.isPending}
            className="gap-2"
          >
            <RefreshCw className={cn('h-4 w-4', syncDevices.isPending && 'animate-spin')} />
            Sync from SSOT
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      {!statsLoading && statsData && (
        <div className="grid gap-4 md:grid-cols-4">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium">Total Devices</CardTitle>
              <Laptop className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{statsData.total}</div>
              <p className="text-xs text-muted-foreground">
                {statsData.uniqueModels} unique models
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium">Deployed</CardTitle>
              <Laptop className="h-4 w-4 text-blue-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-blue-600">
                {statsData.byStatus.deployed || 0}
              </div>
              <p className="text-xs text-muted-foreground">Active in schools</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium">In Stock</CardTitle>
              <Package className="h-4 w-4 text-green-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">
                {statsData.byStatus.in_stock || 0}
              </div>
              <p className="text-xs text-muted-foreground">Available for deployment</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium">In Repair</CardTitle>
              <Wrench className="h-4 w-4 text-yellow-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-yellow-600">
                {statsData.byStatus.repair || 0}
              </div>
              <p className="text-xs text-muted-foreground">
                {statsData.byStatus.retired || 0} retired
              </p>
            </CardContent>
          </Card>
        </div>
      )}

      {/* Filters */}
      <Card>
        <CardContent className="pt-6">
          <div className="flex flex-wrap gap-4 items-end">
            {/* Search */}
            <div className="flex-1 min-w-[200px]">
              <label className="text-sm font-medium text-gray-700 mb-1.5 block">
                Search
              </label>
              <div className="relative">
                <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                <Input
                  placeholder="Search by serial, asset tag, model..."
                  value={searchQuery}
                  onChange={(e) => {
                    setSearchQuery(e.target.value);
                    setPage(0);
                  }}
                  className="pl-9"
                />
              </div>
            </div>

            {/* Status Filter */}
            <div className="w-[180px]">
              <label className="text-sm font-medium text-gray-700 mb-1.5 block">
                Status
              </label>
              <Select
                value={statusFilter}
                onChange={(value) => {
                  setStatusFilter(value);
                  setPage(0);
                }}
                options={statusOptions}
              />
            </div>

            {/* School Filter */}
            <div className="w-[200px]">
              <label className="text-sm font-medium text-gray-700 mb-1.5 block">
                School
              </label>
              <Select
                value={schoolFilter}
                onChange={(value) => {
                  setSchoolFilter(value);
                  setPage(0);
                }}
                options={schoolOptions}
              />
            </div>

            {/* Clear Filters */}
            {hasFilters && (
              <Button variant="ghost" onClick={clearFilters} className="gap-2">
                <X className="h-4 w-4" />
                Clear
              </Button>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Devices Table */}
      <Card>
        <CardHeader className="flex flex-row items-center justify-between">
          <CardTitle>
            Devices {totalDevices > 0 && `(${totalDevices})`}
          </CardTitle>
          {hasFilters && (
            <div className="flex items-center gap-2 text-sm text-muted-foreground">
              <Filter className="h-4 w-4" />
              Filtered
            </div>
          )}
        </CardHeader>
        <CardContent>
          {devicesLoading ? (
            <div className="flex items-center justify-center py-12">
              <div className="h-8 w-8 animate-spin rounded-full border-2 border-gray-300 border-t-blue-600" />
            </div>
          ) : devices.length === 0 ? (
            <div className="text-center py-12">
              <Laptop className="h-12 w-12 text-gray-300 mx-auto mb-3" />
              <p className="text-gray-500">
                {hasFilters ? 'No devices match your filters' : 'No devices found'}
              </p>
              {hasFilters && (
                <Button variant="link" onClick={clearFilters} className="mt-2">
                  Clear filters
                </Button>
              )}
            </div>
          ) : (
            <>
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b bg-gray-50">
                      <th className="px-4 py-3 text-left text-sm font-medium text-gray-600">
                        Serial / Asset Tag
                      </th>
                      <th className="px-4 py-3 text-left text-sm font-medium text-gray-600">
                        Model
                      </th>
                      <th className="px-4 py-3 text-left text-sm font-medium text-gray-600">
                        School
                      </th>
                      <th className="px-4 py-3 text-left text-sm font-medium text-gray-600">
                        Status
                      </th>
                      <th className="px-4 py-3 text-left text-sm font-medium text-gray-600">
                        Updated
                      </th>
                    </tr>
                  </thead>
                  <tbody className="divide-y">
                    {devices.map((device) => (
                      <tr
                        key={device.deviceId}
                        className="hover:bg-gray-50 transition-colors"
                      >
                        <td className="px-4 py-3">
                          <div className="font-mono text-sm font-medium text-gray-900">
                            {device.serial}
                          </div>
                          {device.assetTag && (
                            <div className="text-xs text-gray-500">
                              {device.assetTag}
                            </div>
                          )}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-900">
                          {device.model || '-'}
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-600">
                          {device.schoolId}
                        </td>
                        <td className="px-4 py-3">
                          <Badge className={cn('capitalize gap-1', STATUS_COLORS[device.status] || 'bg-gray-100 text-gray-800')}>
                            {STATUS_ICONS[device.status]}
                            {formatStatus(device.status)}
                          </Badge>
                        </td>
                        <td className="px-4 py-3 text-sm text-gray-500">
                          {new Date(device.updatedAt).toLocaleDateString()}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {/* Pagination */}
              {totalPages > 1 && (
                <div className="flex items-center justify-between pt-4 border-t">
                  <div className="text-sm text-gray-500">
                    Showing {page * limit + 1} to {Math.min((page + 1) * limit, totalDevices)} of {totalDevices}
                  </div>
                  <div className="flex gap-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setPage(p => Math.max(0, p - 1))}
                      disabled={page === 0}
                    >
                      Previous
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setPage(p => Math.min(totalPages - 1, p + 1))}
                      disabled={page >= totalPages - 1}
                    >
                      Next
                    </Button>
                  </div>
                </div>
              )}
            </>
          )}
        </CardContent>
      </Card>

      {/* Models Summary */}
      {models.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Device Models</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex flex-wrap gap-2">
              {models.map((model) => (
                <Badge key={model.model} variant="outline" className="text-sm">
                  {model.model} ({model.count})
                </Badge>
              ))}
            </div>
            {makes.length > 0 && (
              <div className="mt-4 pt-4 border-t">
                <p className="text-sm text-gray-500 mb-2">Makes:</p>
                <div className="flex flex-wrap gap-2">
                  {makes.map((make) => (
                    <span key={make} className="text-sm text-gray-700 bg-gray-100 px-2 py-1 rounded">
                      {make}
                    </span>
                  ))}
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      )}
    </div>
  );
}
