import * as React from 'react';
import { type ColumnDef } from '@tanstack/react-table';
import {
  Package,
  Search,
  RefreshCw,
  Barcode,
  Tag,
  Layers,
} from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Select } from '@/components/ui/select';
import { DataTable, SortableHeader } from '@/components/ui/data-table';
import { usePartsSnapshot, useSyncParts } from '@/api/ssot';
import type { PartSnapshot } from '@/api/ssot';
import { formatDate } from '@/lib/utils';

const categoryColors: Record<string, string> = {
  electronics: 'bg-blue-100 text-blue-800',
  mechanical: 'bg-orange-100 text-orange-800',
  consumable: 'bg-green-100 text-green-800',
  accessory: 'bg-purple-100 text-purple-800',
  replacement: 'bg-yellow-100 text-yellow-800',
};

export function PartsCatalog() {
  const [filters, setFilters] = React.useState<{
    q?: string;
    category?: string;
    limit?: number;
    offset?: number;
  }>({
    limit: 50,
    offset: 0,
  });
  const [searchQuery, setSearchQuery] = React.useState('');

  const { data, isLoading, refetch } = usePartsSnapshot(filters);
  const syncParts = useSyncParts();

  const handleSearch = () => {
    setFilters((prev) => ({ ...prev, q: searchQuery, offset: 0 }));
  };

  const handleSync = () => {
    syncParts.mutate(undefined, {
      onSuccess: () => {
        refetch();
      },
    });
  };

  // Get unique categories for filter
  const categories = React.useMemo(() => {
    if (!data?.items) return [];
    const unique = new Set<string>();
    data.items.forEach((p) => {
      if (p.category) {
        unique.add(p.category);
      }
    });
    return Array.from(unique).map((cat) => ({
      value: cat,
      label: cat.charAt(0).toUpperCase() + cat.slice(1),
    }));
  }, [data?.items]);

  const columns: ColumnDef<PartSnapshot>[] = [
    {
      accessorKey: 'name',
      header: ({ column }) => <SortableHeader column={column}>Part Name</SortableHeader>,
      cell: ({ row }) => (
        <div className="flex items-center gap-3">
          <div className="flex h-8 w-8 items-center justify-center rounded-full bg-amber-50">
            <Package className="h-4 w-4 text-amber-600" />
          </div>
          <div className="min-w-0">
            <div className="font-medium text-gray-900 truncate max-w-[250px]">
              {row.original.name}
            </div>
            <div className="text-sm text-gray-500">{row.original.partId}</div>
          </div>
        </div>
      ),
    },
    {
      accessorKey: 'puk',
      header: ({ column }) => <SortableHeader column={column}>PUK</SortableHeader>,
      cell: ({ row }) => (
        <div className="flex items-center gap-2">
          <Barcode className="h-4 w-4 text-gray-400" />
          <span className="font-mono text-sm">{row.original.puk || '-'}</span>
        </div>
      ),
    },
    {
      accessorKey: 'category',
      header: 'Category',
      cell: ({ row }) => {
        const category = row.original.category || 'unknown';
        return (
          <Badge className={categoryColors[category.toLowerCase()] || 'bg-gray-100 text-gray-800'}>
            <span className="flex items-center gap-1">
              <Layers className="h-3 w-3" />
              {category.charAt(0).toUpperCase() + category.slice(1)}
            </span>
          </Badge>
        );
      },
    },
    {
      accessorKey: 'unit',
      header: 'Unit',
      cell: ({ row }) => (
        <div className="flex items-center gap-2">
          <Tag className="h-4 w-4 text-gray-400" />
          <span>{row.original.unit || '-'}</span>
        </div>
      ),
    },
    {
      accessorKey: 'updatedAt',
      header: ({ column }) => <SortableHeader column={column}>Last Updated</SortableHeader>,
      cell: ({ row }) => (
        <span className="text-gray-600 text-sm">
          {formatDate(row.original.updatedAt)}
        </span>
      ),
    },
  ];

  const items = data?.items || [];
  const total = data?.total || 0;
  const currentPage = Math.floor((filters.offset || 0) / (filters.limit || 50)) + 1;
  const totalPages = Math.ceil(total / (filters.limit || 50));

  // Calculate category counts
  const categoryCounts = React.useMemo(() => {
    const counts: Record<string, number> = {};
    items.forEach((p) => {
      const cat = p.category || 'unknown';
      counts[cat] = (counts[cat] || 0) + 1;
    });
    return counts;
  }, [items]);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Parts Catalog</h1>
          <p className="text-gray-500">Browse and search parts from the SSOT catalog</p>
        </div>
        <Button onClick={handleSync} disabled={syncParts.isPending}>
          <RefreshCw className={`mr-2 h-4 w-4 ${syncParts.isPending ? 'animate-spin' : ''}`} />
          {syncParts.isPending ? 'Syncing...' : 'Sync Parts'}
        </Button>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-500">Total Parts</p>
                <p className="text-2xl font-bold text-gray-900">{total}</p>
              </div>
              <div className="h-12 w-12 rounded-full bg-amber-50 flex items-center justify-center">
                <Package className="h-6 w-6 text-amber-600" />
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-500">Categories</p>
                <p className="text-2xl font-bold text-gray-900">{Object.keys(categoryCounts).length}</p>
              </div>
              <div className="h-12 w-12 rounded-full bg-purple-50 flex items-center justify-center">
                <Layers className="h-6 w-6 text-purple-600" />
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-500">Showing</p>
                <p className="text-2xl font-bold text-gray-900">{items.length}</p>
              </div>
              <div className="h-12 w-12 rounded-full bg-blue-50 flex items-center justify-center">
                <Barcode className="h-6 w-6 text-blue-600" />
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-gray-500">Page</p>
                <p className="text-2xl font-bold text-gray-900">{currentPage} / {totalPages || 1}</p>
              </div>
              <div className="h-12 w-12 rounded-full bg-green-50 flex items-center justify-center">
                <Tag className="h-6 w-6 text-green-600" />
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <div className="flex flex-col sm:flex-row gap-4">
        <div className="flex-1 flex gap-2">
          <div className="relative flex-1 max-w-md">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
            <Input
              placeholder="Search by name or PUK..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && handleSearch()}
              className="pl-9"
            />
          </div>
          <Button variant="outline" onClick={handleSearch}>
            Search
          </Button>
        </div>
        <Select
          value={filters.category || ''}
          onChange={(value) => setFilters((prev) => ({ ...prev, category: value || undefined, offset: 0 }))}
          options={[{ value: '', label: 'All Categories' }, ...categories]}
          className="w-48"
        />
      </div>

      {/* Data Table */}
      <DataTable
        columns={columns}
        data={items}
        isLoading={isLoading}
        emptyMessage="No parts found"
      />

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="flex items-center justify-between">
          <p className="text-sm text-gray-500">
            Showing {(filters.offset || 0) + 1} to {Math.min((filters.offset || 0) + (filters.limit || 50), total)} of {total} parts
          </p>
          <div className="flex gap-2">
            <Button
              variant="outline"
              size="sm"
              disabled={currentPage === 1}
              onClick={() => setFilters((prev) => ({ ...prev, offset: Math.max(0, (prev.offset || 0) - (prev.limit || 50)) }))}
            >
              Previous
            </Button>
            <span className="flex items-center px-3 text-sm text-gray-600">
              Page {currentPage} of {totalPages}
            </span>
            <Button
              variant="outline"
              size="sm"
              disabled={currentPage === totalPages}
              onClick={() => setFilters((prev) => ({ ...prev, offset: (prev.offset || 0) + (prev.limit || 50) }))}
            >
              Next
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}
