import { useEffect } from 'react';
import { Loader2, User, ArrowLeft } from 'lucide-react';
import { Link } from 'react-router-dom';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import { UserProfileCard, UserPermissions, UserPreferencesCard } from '@/components/profile';

export function Profile() {
  const { user, profile, isLoadingProfile, fetchProfile } = useAuth();

  // Fetch profile data on mount
  useEffect(() => {
    if (user && !profile) {
      fetchProfile();
    }
  }, [user, profile, fetchProfile]);

  if (isLoadingProfile) {
    return (
      <div className="flex h-64 items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
      </div>
    );
  }

  // Show a simple view if profile hasn't loaded yet
  if (!profile && user) {
    return (
      <div className="space-y-6">
        <div className="flex items-center gap-4">
          <Link to="/overview">
            <Button variant="ghost" size="sm">
              <ArrowLeft className="mr-2 h-4 w-4" />
              Back
            </Button>
          </Link>
          <div>
            <h1 className="text-2xl font-bold text-gray-900">User Profile</h1>
            <p className="text-sm text-gray-500">
              Your account information
            </p>
          </div>
        </div>

        <div className="rounded-lg border border-gray-200 bg-white p-6">
          <div className="flex items-center gap-4">
            <div className="flex h-16 w-16 items-center justify-center rounded-full bg-blue-100">
              <User className="h-8 w-8 text-blue-600" />
            </div>
            <div>
              <h2 className="text-xl font-semibold text-gray-900">
                {user.displayName || user.username}
              </h2>
              <p className="text-sm text-gray-500">@{user.username}</p>
              {user.email && (
                <p className="text-sm text-gray-500">{user.email}</p>
              )}
            </div>
          </div>
          <div className="mt-4 flex flex-wrap gap-2">
            {user.roles?.map((role) => (
              <span
                key={role}
                className="rounded-full bg-blue-100 px-3 py-1 text-xs font-medium text-blue-800"
              >
                {role}
              </span>
            ))}
          </div>
        </div>

        <div className="flex justify-center">
          <Button onClick={() => fetchProfile()}>
            Load Full Profile
          </Button>
        </div>
      </div>
    );
  }

  if (!profile) {
    return (
      <div className="flex h-64 items-center justify-center">
        <p className="text-gray-500">Unable to load profile</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center gap-4">
        <Link to="/overview">
          <Button variant="ghost" size="sm">
            <ArrowLeft className="mr-2 h-4 w-4" />
            Back
          </Button>
        </Link>
        <div>
          <h1 className="text-2xl font-bold text-gray-900">User Profile</h1>
          <p className="text-sm text-gray-500">
            Your account information and preferences
          </p>
        </div>
      </div>

      {/* Main content grid */}
      <div className="grid gap-6 lg:grid-cols-2">
        {/* Profile card */}
        <UserProfileCard profile={profile} />

        {/* Preferences card */}
        <UserPreferencesCard
          preferences={profile.preferences}
          onEdit={() => {
            // TODO: Open preferences edit modal
            console.log('Edit preferences');
          }}
        />

        {/* Permissions card - full width */}
        <div className="lg:col-span-2">
          <UserPermissions permissions={profile.permissions || []} />
        </div>
      </div>

      {/* Session information */}
      <div className="rounded-lg border border-gray-200 bg-gray-50 p-4">
        <h3 className="mb-2 text-sm font-medium text-gray-700">Session Information</h3>
        <div className="grid gap-4 text-sm sm:grid-cols-2 lg:grid-cols-4">
          <div>
            <span className="text-gray-500">Tenant ID:</span>
            <span className="ml-2 font-mono text-gray-900">{profile.tenantId}</span>
          </div>
          <div>
            <span className="text-gray-500">User ID:</span>
            <span className="ml-2 font-mono text-gray-900">{profile.id}</span>
          </div>
          {profile.ssoSubject && (
            <div>
              <span className="text-gray-500">SSO Subject:</span>
              <span className="ml-2 font-mono text-gray-900">{profile.ssoSubject}</span>
            </div>
          )}
          {profile.createdAt && (
            <div>
              <span className="text-gray-500">Account Created:</span>
              <span className="ml-2 text-gray-900">
                {new Date(profile.createdAt).toLocaleDateString()}
              </span>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
