import { Settings, Bell, Globe, Moon, Sun, Monitor } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import type { UserPreferences } from '@/lib/api';

interface UserPreferencesCardProps {
  preferences?: UserPreferences;
  onEdit?: () => void;
}

// Theme icon mapping
const themeIcons: Record<string, React.ReactNode> = {
  light: <Sun className="h-4 w-4" />,
  dark: <Moon className="h-4 w-4" />,
  system: <Monitor className="h-4 w-4" />,
};

export function UserPreferencesCard({ preferences, onEdit }: UserPreferencesCardProps) {
  const theme = preferences?.theme || 'light';
  const language = preferences?.language || 'en';
  const timezone = preferences?.timezone || 'America/New_York';
  const notifications = preferences?.notifications;

  return (
    <Card>
      <CardHeader>
        <div className="flex items-center justify-between">
          <div>
            <CardTitle className="flex items-center gap-2">
              <Settings className="h-5 w-5" />
              Preferences
            </CardTitle>
            <CardDescription>
              Your dashboard preferences
            </CardDescription>
          </div>
          {onEdit && (
            <Button variant="outline" size="sm" onClick={onEdit}>
              Edit
            </Button>
          )}
        </div>
      </CardHeader>
      <CardContent className="space-y-4">
        {/* Appearance */}
        <div className="flex items-center justify-between rounded-md bg-gray-50 p-3">
          <div className="flex items-center gap-2">
            {themeIcons[theme]}
            <span className="text-sm font-medium text-gray-700">Theme</span>
          </div>
          <span className="text-sm text-gray-600 capitalize">{theme}</span>
        </div>

        {/* Language */}
        <div className="flex items-center justify-between rounded-md bg-gray-50 p-3">
          <div className="flex items-center gap-2">
            <Globe className="h-4 w-4" />
            <span className="text-sm font-medium text-gray-700">Language</span>
          </div>
          <span className="text-sm text-gray-600">
            {language === 'en' ? 'English' : language === 'es' ? 'Spanish' : language}
          </span>
        </div>

        {/* Timezone */}
        <div className="flex items-center justify-between rounded-md bg-gray-50 p-3">
          <div className="flex items-center gap-2">
            <Globe className="h-4 w-4" />
            <span className="text-sm font-medium text-gray-700">Timezone</span>
          </div>
          <span className="text-sm text-gray-600">{timezone}</span>
        </div>

        {/* Notifications */}
        {notifications && (
          <div className="space-y-2">
            <div className="flex items-center gap-2 text-sm font-medium text-gray-700">
              <Bell className="h-4 w-4" />
              <span>Notifications</span>
            </div>
            <div className="ml-6 space-y-1.5">
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-600">Email notifications</span>
                <span className={notifications.emailEnabled ? 'text-green-600' : 'text-gray-400'}>
                  {notifications.emailEnabled ? 'Enabled' : 'Disabled'}
                </span>
              </div>
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-600">Browser notifications</span>
                <span className={notifications.browserEnabled ? 'text-green-600' : 'text-gray-400'}>
                  {notifications.browserEnabled ? 'Enabled' : 'Disabled'}
                </span>
              </div>
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-600">Incident alerts</span>
                <span className={notifications.incidentAlerts ? 'text-green-600' : 'text-gray-400'}>
                  {notifications.incidentAlerts ? 'Enabled' : 'Disabled'}
                </span>
              </div>
              <div className="flex items-center justify-between text-sm">
                <span className="text-gray-600">Work order alerts</span>
                <span className={notifications.workOrderAlerts ? 'text-green-600' : 'text-gray-400'}>
                  {notifications.workOrderAlerts ? 'Enabled' : 'Disabled'}
                </span>
              </div>
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
