import * as React from 'react';
import type { IncidentFilters } from '@/types';

export function useIncidentFilters(initialFilters: IncidentFilters = { limit: 50 }) {
  const [filters, setFilters] = React.useState<IncidentFilters>(initialFilters);
  const [searchQuery, setSearchQuery] = React.useState('');

  const handleFilterChange = React.useCallback(
    (key: keyof IncidentFilters, value: string) => {
      setFilters((prev) => ({
        ...prev,
        [key]: value || undefined,
      }));
    },
    []
  );

  const resetFilters = React.useCallback(() => {
    setFilters(initialFilters);
    setSearchQuery('');
  }, [initialFilters]);

  return {
    filters,
    searchQuery,
    setSearchQuery,
    handleFilterChange,
    resetFilters,
  };
}
