import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import api from './client';
import { toast } from '@/lib/toast';
import type {
  WorkOrder,
  CreateWorkOrderRequest,
  WorkOrderFilters,
  WorkOrderStatus,
  WorkOrderPart,
  WorkOrderSchedule,
  WorkOrderDeliverable,
  WorkOrderApproval,
  PaginatedResponse,
} from '@/types';

const WORK_ORDERS_KEY = 'work-orders';

// List work orders
export function useWorkOrders(filters: WorkOrderFilters = {}) {
  return useQuery({
    queryKey: [WORK_ORDERS_KEY, filters],
    queryFn: () => api.get<PaginatedResponse<WorkOrder>>('/work-orders', filters),
    staleTime: 30_000,
  });
}

// Get single work order
export function useWorkOrder(id: string) {
  return useQuery({
    queryKey: [WORK_ORDERS_KEY, id],
    queryFn: () => api.get<WorkOrder>(`/work-orders/${id}`),
    enabled: !!id,
  });
}

// Create work order
export function useCreateWorkOrder() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (data: CreateWorkOrderRequest) =>
      api.post<WorkOrder>('/work-orders', data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [WORK_ORDERS_KEY] });
      toast.success('Work Order Created', 'The work order has been created successfully');
    },
  });
}

// Update work order status
export function useUpdateWorkOrderStatus() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({ id, status }: { id: string; status: WorkOrderStatus }) =>
      api.patch<WorkOrder>(`/work-orders/${id}/status`, { status }),
    onSuccess: (data) => {
      queryClient.invalidateQueries({ queryKey: [WORK_ORDERS_KEY] });
      queryClient.setQueryData([WORK_ORDERS_KEY, data.id], data);
      toast.success('Status Updated', `Work order status changed to ${data.status.replace('_', ' ')}`);
    },
  });
}

// BOM (Bill of Materials)
export function useWorkOrderBOM(workOrderId: string) {
  return useQuery({
    queryKey: [WORK_ORDERS_KEY, workOrderId, 'bom'],
    queryFn: () => api.get<PaginatedResponse<WorkOrderPart>>(`/work-orders/${workOrderId}/bom`),
    enabled: !!workOrderId,
  });
}

export function useAddBOMItem() {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({
      workOrderId,
      partId,
      qtyPlanned,
      allowIncompatible = false,
    }: {
      workOrderId: string;
      partId: string;
      qtyPlanned: number;
      allowIncompatible?: boolean;
    }) =>
      api.post<WorkOrderPart>(
        `/work-orders/${workOrderId}/bom/items?allowIncompatible=${allowIncompatible}`,
        { partId, qtyPlanned }
      ),
    onSuccess: (_, variables) => {
      queryClient.invalidateQueries({
        queryKey: [WORK_ORDERS_KEY, variables.workOrderId, 'bom'],
      });
    },
  });
}

